   // Fans 
   .fans{
    height: 56vh;
    color: $primarycolor;
    text-align: center;
    padding-top: 16px;
    font-size: 24px;
   
    .fans__title{
      font-family: "Baloo";
      margin-bottom: 16px;
    }
   
    .button__fans{
      background-color: $primarycolor;
        border: none;
        color: #FFFFFF;
        height: 5vh;
        width: auto;
        font-size: 10px;
        border-radius: 20.5px;
        margin-top: 50px;
        display: block;
        margin: auto;
        box-shadow: 4px 4px 0px $thirdcolor;
    }
      
  .fans__img {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15px;
    grid-template-rows: 1fr 1fr;
    padding-top: 32px;
  }
} 
   .star1{
    height: 17.85vh;
    width: auto;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: center;
    justify-self: center;
   }
   
   .star2{
    height: 17.85vh;
    width: auto;
    grid-column: 2 / 2;
    grid-row: minmax (100px, auto);
    align-self: center;
    justify-self: center;
   }
   
   .star3{
    height: 17.85vh;
    width: auto;
    grid-column: 1 / 2;
    grid-row: 2 / 2;
    align-self: center;
    justify-self: center;
   }
   
   .star4{
    height: 17.85vh;
    width: auto;
    grid-column: 2 / 2;
    grid-row: 2 / 2;
    align-self: center;
    justify-self: center;
   }

