      }
      .rrr{
        width: 100vw;
        height: auto;
        left: 0;
        position: absolute;
        z-index: -1;
        top: 56%;
      }
      .rrrr{
        width: 100vw;
        height: auto;
        left: 0;
        position: absolute;
        z-index: -1;
        top: 62%;
       
      }
        .eco__plant, .eco__shop, .eco__earth{
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          margin-bottom: 24px;
          width: auto;
          font-family: "Montserrat Regular";
          font-size: 14px;
        }
        .eco__plant img, .eco__shop img, .eco__earth img{
          margin-bottom: 16px;
        }
        .eco__text__color__thirdcolor{
          color: $thirdcolor;
        }
        .eco__text__color__primarycolor{
          color: $primarycolor;
        }
        .eco__text__color__fourthcolor{
          color: $fourthcolor;
        }
        
    }

}

